<style>
  .pcs-mh-table-w {
    display: flex;
    flex-direction:row;
    flex-wrap:nowrap;
    margin-top: 4px;
  }
  .pcs-mh-th-w {
    border: 1px solid #ececec;
  }
  .pcs-mh-td-w {
    border: 1px solid #ececec;
  }
  .th-t-w, .td-t-w {
    line-height: 36px;
    background-color: #fafafa;
    padding: 0 8px;
    border-bottom: 1px solid #ececec;
    box-sizing: border-box;
  }
  .mh-t-city-list-w, .mh-t-s-item-l-w {
    height: 300px;
    overflow-y: auto;
  }
  .sys-set-w .el-form-item {
    margin-bottom: 8px;
  }
  .c-l-item-w, .s-item-l-w {
    line-height: 40px;
    padding: 0 8px;
  }
  .c-l-item-w {
    cursor: pointer;
    border-bottom: 1px solid #ececec;

    display: flex;
    flex-direction:row;
    flex-wrap:nowrap;
  }
  .c-l-item-w.is-active {
    background-color: #2077FF;
    color: #fff;
  }
  .c-l-item-w:hover {
    background-color: #66b1ff;
    color: #fff;
  }

  .sys-set-w .has-gutter>tr>th {
    padding: 0 !important;
    background-color: #fafafa;
  }

  .sys-set-w .el-table td, .sys-set-w .el-table th.is-leaf {
    border-bottom: 0;
  }
</style>
<template>
  <div class="sys-set-w">
    <!-- 操作框 -->
    <div class="handel-search-wraper">
      <div class="handel-wraper">
        清理指定平台/城市/门店三方商品
      </div>
      <hr class="s-slip">
      <div style="padding: 10px;">
        <el-form  @submit.native.prevent :model="publishParamMesg" label-position="right" :label-width="formLabelWidth" v-loading="loading">
          <el-form-item label="指定删除平台" required>
            <el-checkbox-group v-model="publishParamMesg.checkedPlatform">
              <el-checkbox v-for="item in platformList" :label="item.platformId" :key="item.platformId">{{item.name}}</el-checkbox>
            </el-checkbox-group>
          </el-form-item>

          <el-form-item label="指定删除城市/门店" required>
            <div class="pcs-mh-table-w">
              <div class='pcs-mh-td-w'>
                <div class='td-t-w'>
                  <span>
                    门店列表
                  </span>
                  <el-input
                    style="float:right;"
                    class="search-input"
                    placeholder="ERP编号/门店名称"
                    v-model="searchContent.searchValue"
                    @keyup.enter.native="getStoreList"
                    suffix-icon="el-icon-search"
                    clearable
                    size="mini">
                  </el-input>
                </div>
                <div class="mh-t-s-item-l-w">
                  <el-table
                    ref="multiple"
                    height="300px"
                    :data="storeOptions"
                    @select="handleSelect"
                    @select-all="handleSelectAll"
                    empty-text="没有查询到您想要的数据"
                    size="small">
                    <el-table-column
                      type="selection"
                      width="50">
                    </el-table-column>
                    <el-table-column prop="storeName" label="门店名称" width="320"></el-table-column>
                  </el-table>                
                </div>
              </div>
            </div>
          </el-form-item>
        </el-form>    
        <hr class="s-slip" style="margin-top:20px;">
        <div style="padding:10px;">
          <el-button type="primary" @click="doDeleteHandel" size="mini" icon="el-icon-delete" style="margin-left:140px;" :disabled="isDisabled">立即删除</el-button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import * as utils from '@/utils/utils'
  const sysSettingUrl = {
    // 获取商家平台
    getPlatformList: '/plateform/a/product/getPlatformList',
    // 获取门店列表接口
    search: '/plateform/a/store/condition/search',
    // 删除清理指定平台 门店三方商品接口
    syncdel: '/plateform/a/product/syncdel'
  }
  export default {
    data() {
      return {
        loading: false, 
        formLabelWidth: '150px',
        // 选择发布平台信息
        publishParamMesg: {
          checkedPlatform: []
        },
        platformList: [], // 平台列表
        searchContent: {
          searchValue: '' // 搜索条件ERP编码/门店名称关键字
        },
        isDisabled: false, // 按钮是否可点击
        // 门店列表
        storeOptions: [],
        checkedStores: [] // 选中门店

      }
    },
    created() {
      // 获取平台列表
      this.getPlatformList()
      // 获取所有门店
      this.getStoreList()
    },
    methods: {
      /**
       * [getPlatformList 获取平台列表]
       * @return {[type]} [description]
       */
      getPlatformList() {
        this.loading = true
        this.$http.get(sysSettingUrl.getPlatformList).then(res => {
          this.loading = false
          if(res.code === 0) {
            if(res.data && res.data.length) {
              this.platformList = res.data
            }else {
              this.platformList = []
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.loading = false
          console.log("提示(获取平台列表):", e.message)
        })
      },
      /**
       * [getStoreList 获取门店列表]
       * @return {[type]} [description]
       */
      getStoreList() {
        this.$http.post(sysSettingUrl.search, {...this.searchContent}).then(res => {
          if(res.code === 0) {
            if(res.data && res.data.length) {
              let optionsArr = []
              for(let item of res.data) {
                let optionItem = {
                  cityId: item.cityId,
                  storeNo: item.storeNo,
                  storeName: item.storeNo +'-'+ item.storeName
                }
                optionsArr.push(optionItem)
              }
              this.storeOptions = optionsArr
            }else {
              this.storeOptions = []
            }
            this.defauleSelectFun()
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          console.log("提示(获取门店列表):", e.message)
        })
      },
      /**
       * [handleSelect 选择某一个]
       * @return {[type]} [description]
       */
      handleSelect(val, row) {
        if(utils.indexInArr(this.checkedStores, row.storeNo) > -1) {
          // 取消选择
          this.checkedStores = utils.removeElFromArr(this.checkedStores, row.storeNo)
          return
        }else {
          // 选择
          // 临时缓存使用
          let checkedStoresListTemp = []
          checkedStoresListTemp.push(row.storeNo)
          // 获取去重后的上传的参数
          let tempStoreNoArr = [...checkedStoresListTemp, ...this.checkedStores]
          this.checkedStores = Array.from(new Set(tempStoreNoArr))
        }
      },
      /**
       * [handleSelectAll 选择多个]
       * @return {[type]} [description]
       */
      handleSelectAll(val) {
        if(val.length > 0) {
          // 全选
          for(let item of val) {
            this.checkedStores.push(item.storeNo)
          }
          // 去重
          this.checkedStores = Array.from(new Set(this.checkedStores))
        }else { 
          // 全不选 此时val.length<=0
          // 1.获取当前table中每一条的id
          let storeNoList = []
          for(let i of this.storeOptions) {
            storeNoList.push(i.storeNo)
          }
          // 2.从要传递的参数中去掉这些id
          this.checkedStores = Array.from(new Set([...this.checkedStores].filter(j => !new Set(storeNoList).has(j))))
        }
      },
      /**
       * [defauleSelectFun 设置表格中的默认选中项]
       * @param    {[type]}                 row [description]
       * @return   {[type]}                     [description]
       */
      defauleSelectFun() {
        let _this = this
        let rows = []
        for(let item of this.storeOptions) {
          for(let key of this.checkedStores) {
            if(item.storeNo == key) {
              rows.push(item)
            }
          }            
        }
        if (rows.length>0) {
          this.$refs.multiple.clearSelection()
          setTimeout(function() {
            rows.forEach(rowItem => {
              _this.$refs.multiple.toggleRowSelection(rowItem)
            });
          }, 100)
        } else {
          this.$refs.multiple.clearSelection()
        }
      },
      /**
       * [doDeleteHandel 执行删除清理指定平台/城市/门店三方商品 方法]
       * @return {[type]} [description]
       */
      doDeleteHandel() {
        if(this.publishParamMesg.checkedPlatform.length === 0) {
          this.$message("请选择指定删除平台")
          return
        }
        if(this.checkedStores.length === 0) {
          this.$message("请选择指定删除城市/门店")
          return
        }
        if(this.isDisabled === true) {
          return
        }
        let _params = {
          platformList: this.publishParamMesg.checkedPlatform,
          storeNoList: this.checkedStores
        }
        // 保存按钮不可再次点击
        this.isDisabled = true
        this.$http.post(sysSettingUrl.syncdel, _params).then(res => {
          // 保存按钮可再次点击
          let _this = this
          setTimeout(function() {
            _this.isDisabled = false
          }, 800)
          if(res.code === 0) {
            this.$message.success("清理指定平台/城市/门店三方商品成功")
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          // 保存按钮可再次点击
          let _this = this
          setTimeout(function() {
            _this.isDisabled = false
          }, 800)
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      }


    }
    
  }
</script>


